<template>
  <div class="detail">
    <van-nav-bar
      :title="json.num"
      left-text="返回"
      left-arrow
      :fixed="true"
      @click-left="back"
    />
    <!-- 图片盒子 -->
    <div id="img_box" @click="choseType">
      <img :src="v.img" v-for="(v, i) in list" :key="i" />
    </div>
    <!-- 操作按钮 -->
    <div class="nav" v-if="isShow">
      <div class="morezj">
        <van-button round color=" #49dda1" @click="more" type="info"
          >查看目录</van-button
        >
      </div>
      <div class="nextZj">
        <van-button round color="#000" @click="prevGet" type="info"
          >上一话</van-button
        >
        <van-button round color="#000" @click="nextGet" type="info"
          >下一话</van-button
        >
      </div>
    </div>

    <!-- 选择章节 -->
    <van-popup
      v-model="popup"
      @close="cancle"
      :duration="0.2"
      round
      position="bottom"
      class="popupClass"
      :style="{ height: '70%' }"
    >
      <div>
        <van-row>
          <van-col span="8" v-for="(item, i) in mhlist" :key="i">
            <div class="list_box">
              <router-link
                :to="{
                  path: '/detail',
                  query: { url: item.url, num: item.num },
                }"
                :class="index == i ? 'span active' : 'span'"
                @click.native="goDetail(item)"
                >{{ item.tit }}</router-link
              >
            </div>
          </van-col>
        </van-row>
      </div>
    </van-popup>
  </div>
</template>

<script>
import '../assets/scss/deail.scss';
import { mhDetailsApi } from "@/api/api";
export default {
  name: "",
  data() {
    return {
      allList: [], //所有的图片
      list: [], //图片列表
      json: {}, //当前章节
      isShow: false, //是否显示操作栏
      popup: false, //是否显示章节列表
      mhlist: [], //该漫画所有章节列表
      index: null, //当前漫画的章节
      timer: true, //延时执行
      page: {
        pageSize: 5, //页数
        pageNo: 1, //页码
        maxPage: 1, //最大页数
      }, //分页
      pageIsOver: false, //当前章节分页是否结束
      imgHeight: 0, //通过这个来判断是否进行懒加载
    };
  },
  methods: {
    back() {
      let url = window.sessionStorage.getItem("parentUrl");
      this.$router.push({
        path: "/about",
        query: {
          url: url,
        },
      });
    },
    cancle() {
      this.isShow = false;
      this.popup = false;
    },
    choseType() {
      this.isShow ? (this.isShow = false) : (this.isShow = true);
    },
    //页面数据初始化
    getData() {
      this.init();
      let { pageSize, pageNo } = this.page;
      let { url } = this.json;
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });
      mhDetailsApi(url).then((res) => {
        
        let { code, list } = res.data;
        if (code === 0) {
          this.allList = list;
          this.list = list.slice(
            parseInt(pageNo.toString()) - 1,
            parseInt(pageSize.toString())
          );
          this.page = {
            pageSize: pageSize,
            pageNo: pageNo,
            maxPage: Math.ceil(list.length / parseInt(pageSize)),
          };
        }
        
        this.$toast.clear();
      });
    },
    //页面数据初始化2
    getNextData() {
      this.init();
      let { pageSize, pageNo } = this.page;
      let { url } = this.json;
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });
      mhDetailsApi(url).then((res) => {
        let { code, list } = res.data;
        if (code === 0) {
          this.pageIsOver = false;
          this.isShow = false;
          this.popup = false;
          this.allList = [...this.allList, ...list];
          this.list = this.allList.slice(0, (pageNo + 1) * pageSize);
          this.page = {
            pageSize: pageSize,
            pageNo: pageNo + 1,
            maxPage: Math.ceil(this.allList.length / parseInt(pageSize)),
          };
        }
        this.$toast.clear();
      });
    },
    more() {
      let { url } = this.json;
      this.mhlist = JSON.parse(window.sessionStorage.getItem("mhList"));
      this.mhlist.forEach((v, i) => {
        if (v.url === url) {
          this.index = i;
          return false;
        }
      });
      this.popup = true;
    },
    goDetail(item) {
      Object.assign(this.json, item);
      this.getData();
    },
    /*
     * 获取下一章节信息
     */
    getNextUrl() {
      let { url} = this.json;
      let list = JSON.parse(window.sessionStorage.getItem("mhList"));
     
     
     this.index++
      let nextJson = null;
      list.forEach((v, i) => {
        if (url === v.url) {
          if (i + 1 >= list.length) return false; //如果是最后一章 直接弹出。
          
          nextJson = list[i + 1];
        }
      });
      if (nextJson === null) {
        return false;
      } else {
         
        return nextJson;
        
      }
    },
    /*
     * 获取上一章节信息
     */
    getPrevUrl() {
      let { url } = this.json;
      let list = JSON.parse(window.sessionStorage.getItem("mhList"));
      let nextJson = null;
      
     
      list.forEach((v, i) => {
        if (url === v.url) {
          if (i - 1 < 0) return false; //如果是最后一章 直接弹出。
          nextJson = list[i - 1];
        }
      });
      if (nextJson === null) {
        return false;
      } else {
        
        return nextJson;
      }
    },
    /*
     * 调用分页
     */
    pageChange() {
      let {
        allList,
        page: { pageSize, pageNo, maxPage },
      } = this;
      pageNo++;
      if (pageNo <= maxPage) {
        this.page = {
          pageNo: pageNo,
          pageSize: pageSize,
          maxPage: maxPage,
        };
        this.list = allList.slice(0, pageNo * pageSize);
      } else {
        if (!this.pageIsOver) {
          this.$toast("到底了");
          this.pageIsOver = true;
          this.nextGet(true);
        }
      }
    },
    /*
     *监听下拉
     */
    scroll() {
      let box = document.querySelector("#img_box");
      let scrollTop =
        document.documentElement.scrollTop || document.body.scrollTop;
      let pageHeight = window.screen.availHeight;
      if (!this.imgHeight && box.children[0]) {
        this.imgHeight = box.children[0].clientHeight * 1.5;
      }
      if (box == null) return;
      let h = box.clientHeight;
      if (scrollTop + pageHeight + this.imgHeight > h) {
        clearTimeout(this.timer);
        this.timer = setTimeout(() => {
          this.pageChange(); //开始分页
        }, 300);
        return;
      }
    },
    init() {
      this.page = {
        pageSize: 5, //页数
        pageNo: 1, //页码
        maxPage: 1, //最大页数
      };
      this.pageIsOver = false;
      this.isShow = false;
      this.popup = false;
      this.$nextTick(() => {
        document.documentElement.scrollTop
          ? (document.documentElement.scrollTop = 0)
          : (document.body.scrollTop = 0);
      }); //滚动条清零
    },
    /*
     * 上一章
     */
    prevGet() {
      if (!this.getPrevUrl()) {
        this.$toast("没有上一章");
        return;
      } //检测是否是第一话
      this.json = this.getPrevUrl();
      this.getData();
    },
    /*
     * 下一章
     */
    nextGet(isNext) {
      if (!this.getNextUrl()) {
        this.$toast("最后一话!");
        return;
      } //检测是否是最后一章
      this.json = this.getNextUrl();
      if (isNext) {
        this.getNextData();
      } else {
        this.getData();
      }
    },
  },
  created() {
      let { url, num } = this.$route.query;
    this.json = {
      url: url,
      num: num,
    };
    this.getData();
    
  },
  mounted() {
    window.addEventListener("scroll", this.scroll, false);
  },
  beforeDestroy() {
    if (window.removeEventListener) {
      window.removeEventListener("scroll", this.scroll, false);
    } else if (window.attachEvent) {
      window.attachEvent("scroll", this.scroll, false);
    }
    
  },
};
</script>

